export const addTable = (table) => {
	const tid = table.tid;
	const orders = table.addedEntries;
	console.log(table);
	console.log('there');
	if (!tid) {
		console.log('here');
		return (dispatch, getState, { getFirebase, getFirestore }) => {
			dispatch({ type: 'ADD_TABLE_ERROR', orderError: 'Masa Seçmelisin.' });
		};
	}
	if (orders.length === 0) {
		return (dispatch, getState, { getFirebase, getFirestore }) => {
			dispatch({ type: 'ADD_TABLE_ERROR', orderError: 'Ürün seçmelisin.' });
		};
	}
	return (dispatch, getState, { getFirestore }) => {
		console.log('mucize');
		const firestore = getFirestore();

		firestore.collection('tables').doc(tid).update({
			isTaken: true,
		});
		orders.forEach((order) => {
			const firestore = getFirestore();
			firestore
				.collection('tables')
				.doc(tid)
				.collection('orders')
				.add({
					...order,
				})
				.then(() => {
					dispatch({
						type: 'ADD_TABLE',
						orderError: '',
						orderSuccess: 'success',
						table,
					});
				})
				.catch((error) =>
					dispatch({
						type: 'ADD_TABLE_ERROR',
						orderError: error,
						orderSuccess: '',
						table,
					})
				);
		});
	};
};
